.container {
  margin: 30px 0;
}

.card {
  margin: 0 2% !important;
}

.infected {
  border-top: 10px solid rgba(0, 0, 255, 0.5);
  border-bottom: 10px solid rgba(0, 0, 255, 0.5);
  transform: translateZ(0);
  transition: transform 0.25s ease-out;
}

.recovered {
  border-top: 10px solid rgba(0, 255, 0, 0.5);
  border-bottom: 10px solid rgba(0, 255, 0, 0.5);
  transform: translateZ(0);
  transition: transform 0.25s ease-out;
}

.deaths {
  border-top: 10px solid rgba(255, 0, 0, 0.7);
  border-bottom: 10px solid rgba(255, 0, 0, 0.7);
  transform: translateZ(0);
  transition: transform 0.25s ease-out;
}

.infected:hover,
.recovered:hover,
.deaths:hover {
  transform: scale(1.05);
}

.error {
  color: darkred;
}
@media (max-width: 960px) {
  .card {
    margin: 2% 10px !important;
  }
}
